	module.exports = {"data":{"markdownRemark":{"html":"<p>Nếu bạn đã thông thạo hết React, thì không có lý do vì bạn dưừng lại ở React JS mà không tiến lên React Native.</p>\n<h1 id=\"setup-môi-trường\"><a href=\"#setup-m%C3%B4i-tr%C6%B0%E1%BB%9Dng\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Setup môi trường</h1>\n<p><a href=\"https://habiletechnologies.com/blog/getting-started-react-native-complete-setup-guide/\">React Native Environment setup (iOS and Android) </a></p>\n<h1 id=\"tuts-bắt-đầu-với-react-native\"><a href=\"#tuts-b%E1%BA%AFt-%C4%91%E1%BA%A7u-v%E1%BB%9Bi-react-native\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Tuts Bắt đầu với React native</h1>\n<p><a href=\"https://www.raywenderlich.com/165140/react-native-tutorial-building-ios-android-apps-javascript\">React Native Tutorial: Building iOS and Android apps with Javascript</a></p>\n<p><a href=\"https://school.shoutem.com/lectures/build-react-native-mobile-app-tutorial/\">Shoutem Tutorial on React Native: Build your first Mobile app</a></p>\n<p><a href=\"https://rationalappdev.com/react-native-cheat-sheet/\">React Native Cheat Sheet </a></p>\n<h1 id=\"navigation-trong-react-native-bằng-react-navigation\"><a href=\"#navigation-trong-react-native-b%E1%BA%B1ng-react-navigation\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Navigation trong React Native bằng React Navigation</h1>\n<p><a href=\"https://hackernoon.com/getting-started-with-react-navigation-the-navigation-solution-for-react-native-ea3f4bd786a4\">Getting started with React Navigation </a></p>\n<p><a href=\"https://www.reactnative.guide/10-navigation/10.1-using-react-navigation.html\">Using React Navigation</a></p>\n<h1 id=\"debug\"><a href=\"#debug\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Debug</h1>\n<p>Đơn giản nhất để debug là dùng <a href=\"https://github.com/jhen0409/react-native-debugger\">React native debugger</a></p>\n<p>Đọc thêm cách sử dụng</p>\n<ul>\n<li><a href=\"https://medium.com/research-engineering-at-simply-technologies/react-native-debugging-like-a-god-9610ac2ffd12\">Debug your React Native application like a God </a></li>\n<li><a href=\"https://blog.reactnativecoach.com/debugging-react-native-and-redux-with-react-native-debugger-62f6ceef3033\">Debugging React and Redux with React Native Debugger</a></li>\n<li><a href=\"https://github.com/infinitered/reactotron\">Tài liệu chính thức</a></li>\n<li><a href=\"https://www.youtube.com/watch?v=tPBRfxswDjA\">Video nếu thấy đọc chưa hiểu lắm</a></li>\n</ul>\n<h1 id=\"xây-dựng-một-số-ứng-dụng-react-native-đơn-giản\"><a href=\"#x%C3%A2y-d%E1%BB%B1ng-m%E1%BB%99t-s%E1%BB%91-%E1%BB%A9ng-d%E1%BB%A5ng-react-native-%C4%91%C6%A1n-gi%E1%BA%A3n\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Xây dựng một số ứng dụng React Native đơn giản</h1>\n<p><a href=\"https://learn.handlebarlabs.com/p/react-native-basics-build-a-currency-converter\">Build a simple Currency convertor in React Native for iOS</a></p>\n<p><a href=\"https://code.tutsplus.com/tutorials/creating-a-dictionary-app-using-react-native-for-android--cms-24969\">Build a dictionary app in React Native for Android</a></p>\n<h1 id=\"cải-thiện-performance\"><a href=\"#c%E1%BA%A3i-thi%E1%BB%87n-performance\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Cải thiện performance</h1>\n<p>2 điều cần nhớ khi bắt đầu cải thiện performance với React Native</p>\n<ul>\n<li>\n<p><strong>Bridge optimize</strong>: ở giữa javascript threat và native threat của ứng dụng React native sẽ có một cái <strong>Bridge</strong> ở giữa để chuyển đổi giữa 2 threat này. Phần lớn workload nặng nhất là ở đây, những animation chạy trên single threat của javascript sẽ rất tốn kém. Nếu muốn nghiên cứu sâu hơn về kiến trúc của React Native, ngâm cứu bài <a href=\"https://tadeuzagallo.com/blog/react-native-bridge/\">https://tadeuzagallo.com/blog/react-native-bridge/</a></p>\n</li>\n<li>\n<p><strong>Quản lý view phức tạp</strong> nếu app chỉ có một vài view thì chả có gì phải cải thiện. Nếu với nhiều view phức tạp, navigation lung tung, chuyện để ý tới performance là bất buộc.</p>\n</li>\n</ul>\n<p>Một tài liệu để đọc</p>\n<p><a href=\"https://facebook.github.io/react-native/docs/performance.html#common-sources-of-performance-problems\">Dive into React Native performance</a></p>\n<p><a href=\"https://www.youtube.com/watch?v=9VqVv_sVgv0\">how to optimize the performance of your React Native app</a></p>\n<p><a href=\"https://www.simform.com/react-native-app-performance/\">React Native performance issues</a></p>\n<h1 id=\"test\"><a href=\"#test\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Test</h1>\n<p><a href=\"https://pillow.codes/testing-in-react-native-jest-detox-d7b3b79a166a\">Testing in React Native using Jest and Detox</a></p>\n<p><a href=\"https://chase-seibert.github.io/blog/2017/01/06/appium-react-native-quickstart.html\">Appium + React Native Quickstart</a></p>\n<p><a href=\"https://www.skcript.com/svr/testing-react-native-apps-with-appium-the-ultimate-guide/\">Testing React Native Apps with Appium</a></p>\n<p><a href=\"https://alligator.io/react/roadmap-react-native-developer/\">Link bài viết gốc tác giả Rakshit Sora</a></p>","timeToRead":3,"excerpt":"Nếu bạn đã thông thạo hết React, thì không có lý do vì bạn dưừng lại ở React JS mà không tiến lên React Native. Setup môi trường React…","frontmatter":{"title":"Con đường để trở thành React Native developer ( phiên bản 2018)","cover":"","date":"2018-08-13","category":null,"tags":["react","javascript","react-native"],"desc":"Tiếp theo bài trước mình sẽ dịch thêm một bài để các bạn bắt đầu học lập trình React Native"},"fields":{"slug":"/2018-08-13-huong-dan-con-dong-hoc-lap-trinh-react-native"}}},"pathContext":{"slug":"/2018-08-13-huong-dan-con-dong-hoc-lap-trinh-react-native","prev":{"frontmatter":{"title":"Flow sử lý trong modern JS - từ callback đến promise, đến Async/Await","desc":"Cùng nhìn lại quá trình tiến hóa của javascript trong cách sử lý flow","type":"post","category":null,"tags":["javascript"],"date":"2018-08-14","cover":""},"fields":{"slug":"/2018-08-14-huong-dan-flow-xy-ly-trong-modern-js-callback-promise-async-await"}},"next":{"frontmatter":{"title":"Con đường để trở thành React developer ( phiên bản 2018)","desc":"Hướng dẫn, soi sáng cho các bạn muốn trở thành FrontEnd Developer. Nếu đã chọn FrontEnd làm nghiệp, React làm món ăn mỗi sáng, mà chưa biết cần học cái gì, hy vọng bài viết này sẽ có ích cho các bạn còn đang ko biết học cái gì","type":"post","category":null,"tags":["react","javascript"],"date":"2018-08-12","cover":""},"fields":{"slug":"/2018-08-12-huong-dan-con-duong-tro-thanh-developer-2018"}}}}

